<template>
  <main>
    <h1>Your Consumers</h1>
    <p>
      In this page you can see all the consumers you have, and the associated
      information. You can change them by clicking on the lock icon, and save
      changes by clicking on the database icon. You can also delete one or more
      consumer, but be aware: the last operation <em>can not</em> be undone!
    </p>

    <table v-if="consumers.length !== 0">
      <thead>
        <tr>
          <th>Username</th>
          <th>Email</th>
          <th>Name &amp; Surname</th>
          <th>Password</th>
          <th>Edit</th>
          <th>Save</th>
          <th>Delete</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="(consumer, index) in consumers" :key="consumer.username">
          <td>
            <input
              type="text"
              v-model="consumer.username"
              class="inactive"
              readonly
            />
          </td>
          <td>
            <input
              type="email"
              v-model="consumer.email"
              class="inactive"
              readonly
            />
          </td>
          <td>
            <input
              type="text"
              v-model="consumer.nameSurname"
              class="inactive"
              readonly
            />
          </td>
          <td>
            <input
              type="password"
              v-model="consumer.password"
              class="inactive"
              readonly
            />
          </td>
          <td>
            <button title="edit a consumer" @click="changeConsumer(index + 1)">
              <img src="closed_lock.png" alt="closed_lock" />
            </button>
          </td>
          <td>
            <button
              title="commit edit changes"
              @click="commitChanges(consumer)"
            >
              <img src="commit_changes.png" alt="commit_changes" />
            </button>
          </td>
          <td>
            <button
              title="delete definitively a consumer"
              @click="deleteConsumer(consumer.username)"
            >
              <img src="delete_button.png" alt="bin" />
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <p v-else>
      Unfortunately, you don't have any affiliate consumer. Go
      <router-link class="router-link" to="/addconsumer">here</router-link> to
      add a new consumer
    </p>
  </main>
</template>

<script scoped>
import Axios from "axios";
export default {
  name: "Consumers",
  components: {},
  methods: {
    changeConsumer: function (rowNumber) {
      let emailElement = document.querySelector(
        `table tbody tr:nth-of-type(${rowNumber}) td:nth-child(2) input`
      );
      let nameSurnameElement = document.querySelector(
        `table tbody tr:nth-of-type(${rowNumber}) td:nth-child(3) input`
      );
      let passwordElement = document.querySelector(
        `table tbody tr:nth-of-type(${rowNumber}) td:nth-child(4) input`
      );

      if (passwordElement.readOnly === true) {
        /*changing style*/
        emailElement.className = "active";
        nameSurnameElement.className = "active";
        passwordElement.className = "active";
        passwordElement.type = "text";
        emailElement.readOnly = false;
        nameSurnameElement.readOnly = false;
        passwordElement.readOnly = false;
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt="closed_lock"]`
        ).src = "opened_lock.png";
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt='closed_lock']`
        ).alt = "opened_lock";
      } else {
        emailElement.className = "inactive";
        nameSurnameElement.className = "inactive";
        passwordElement.className = "inactive";
        passwordElement.type = "password";

        emailElement.readOnly = true;
        nameSurnameElement.readOnly = true;
        passwordElement.readOnly = true;
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt="opened_lock"]`
        ).src = "closed_lock.png";
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt='opened_lock']`
        ).alt = "closed_lock";
      }
    },

    commitChanges: function (consumer) {
      Axios.put(`${process.env.VUE_APP_APIROOT}uploaderarea/consumer`, consumer)
        .then(() => alert("changes saved!"))
        .catch(() => alert("something went wrong..."));
    },

    deleteConsumer: function (username) {
      Axios.delete(
        `${process.env.VUE_APP_APIROOT}uploaderarea/consumer/${username}`
      )
        .then(() => {
          alert("consumer deleted correctly");
          this.consumers.filter((consumer) => consumer.username !== username);
        })
        .catch(() => alert("something went wrong"));
    },
  },
  data: function () {
    return {
      consumers: [],
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/consumers`)
      .then((response) => (this.consumers = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

